"use client";

import React, { useEffect, useMemo, useState } from "react";
import { AffiliateAPI } from "@/utils/api";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Skeleton } from "@/components/ui/skeleton";
import { useToast } from "@/components/ui/toast";
import { SectionCard, StatCard } from "../_components/cards";
import { useAffiliateDashboardContext } from "../_context/affiliate-dashboard-context";

type PayoutRow = {
  id: number;
  month?: string;
  percent?: number;
  amount: number;
  status: string;
};

const statusOptions = [
  { value: "", label: "All statuses" },
  { value: "pending", label: "Pending" },
  { value: "approved", label: "Approved" },
  { value: "paid", label: "Paid" },
];

export default function AffiliatePayoutsPage() {
  const { show } = useToast();
  const { stats, monthLabel, payoutPercent, actions, payoutMonth, setPayoutMonth, setPayoutPercent } = useAffiliateDashboardContext();

  const [statusFilter, setStatusFilter] = useState<string>("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [rows, setRows] = useState<PayoutRow[]>([]);

  const [requesting, setRequesting] = useState(false);

  useEffect(() => {
    refresh();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusFilter]);

  async function refresh() {
    setLoading(true);
    try {
      const data = await AffiliateAPI.payouts(statusFilter || undefined);
      setRows(Array.isArray(data) ? data : []);
      setError(null);
    } catch (err: any) {
      setError(err?.message || "Failed to load payouts");
    } finally {
      setLoading(false);
    }
  }

  async function submitPayoutRequest(e: React.FormEvent) {
    e.preventDefault();
    setRequesting(true);
    try {
      await actions.requestPayout();
      setStatusFilter("pending");
      refresh();
    } catch (err: any) {
      show({ variant: "destructive", title: "Request failed", description: err?.message || "Unable to submit payout" });
    } finally {
      setRequesting(false);
    }
  }

  const totalRequested = useMemo(
    () => rows.reduce((sum, row) => sum + (row.amount || 0), 0),
    [rows]
  );

  const filteredRows = rows;

  return (
    <div className="space-y-6">
      <SectionCard
        title="Payout overview"
        description={`Snapshot for ${monthLabel.toLowerCase()} including pending and paid amounts.`}
      >
        <div className="grid gap-4 sm:grid-cols-2 xl:grid-cols-4">
          <StatCard title="Current commission" value={formatCurrency(stats.currentCommission)} icon={null} description="Earned this period" />
          <StatCard title="Pending payout" value={formatCurrency(stats.pendingPayout)} icon={null} description="Awaiting approval" />
          <StatCard title="Paid to date" value={formatCurrency(stats.paidPayout)} icon={null} description="All-time payouts" />
          <StatCard title="Requests loaded" value={filteredRows.length} icon={null} description="Visible in table" />
        </div>
      </SectionCard>

      <SectionCard
        title="Request a payout"
        description="Choose the month and commission percentage you&apos;re eligible for, then submit the request."
      >
        <form onSubmit={submitPayoutRequest} className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
          <div className="sm:col-span-1">
            <label className="text-xs font-medium text-emerald-600">Month (YYYY-MM)</label>
            <Input
              required
              value={payoutMonth}
              onChange={(event) => setPayoutMonth(event.target.value)}
              placeholder="2025-10"
              className="mt-2 h-11 rounded-xl border-emerald-200 bg-white/90 focus:border-emerald-400 focus:ring-emerald-400"
            />
          </div>
          <div className="sm:col-span-1">
            <label className="text-xs font-medium text-emerald-600">Commission percent</label>
            <Input
              type="number"
              min={1}
              max={20}
              step={0.5}
              value={payoutPercent}
              onChange={(event) => setPayoutPercent(Number(event.target.value))}
              className="mt-2 h-11 rounded-xl border-emerald-200 bg-white/90 focus:border-emerald-400 focus:ring-emerald-400"
            />
          </div>
          <div className="sm:col-span-1">
            <label className="text-xs font-medium text-emerald-600">Status filter</label>
            <select
              value={statusFilter}
              onChange={(event) => setStatusFilter(event.target.value)}
              className="mt-2 h-11 w-full rounded-xl border border-emerald-200 bg-white/90 px-3 text-sm text-emerald-800 focus:border-emerald-400 focus:outline-none"
            >
              {statusOptions.map((option) => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>
          <div className="flex items-end justify-end sm:col-span-2 lg:col-span-1">
            <Button
              type="submit"
              disabled={requesting}
              className="h-11 w-full rounded-xl bg-emerald-600 text-sm font-semibold text-white shadow-lg transition hover:bg-emerald-700 disabled:cursor-not-allowed disabled:bg-emerald-200"
            >
              {requesting ? "Submitting..." : "Submit request"}
            </Button>
          </div>
          <div className="sm:col-span-2 lg:col-span-4 text-xs text-emerald-700/80">
            Finance reviews payout requests within two business days. You&apos;ll receive an email update once the status changes.
          </div>
        </form>
      </SectionCard>

      <SectionCard
        title="Payout history"
        description={`Showing ${filteredRows.length} request${filteredRows.length === 1 ? "" : "s"}. Total loaded amount ${formatCurrency(totalRequested)}.`}
      >
        {loading ? (
          <Skeleton className="h-56 rounded-2xl" />
        ) : error ? (
          <div className="rounded-2xl border border-red-200 bg-red-50/80 p-6 text-sm text-red-600">{error}</div>
        ) : filteredRows.length === 0 ? (
          <div className="rounded-2xl border border-dashed border-emerald-200 bg-emerald-50/60 p-8 text-center text-sm text-emerald-700/80">
            No payout requests yet. Submit a request above to get started.
          </div>
        ) : (
          <div className="overflow-hidden rounded-2xl border border-emerald-100/70">
            <table className="min-w-full divide-y divide-emerald-100/70 text-sm text-emerald-900">
              <thead className="bg-emerald-50/70 text-xs uppercase tracking-wide text-emerald-600">
                <tr>
                  <th className="px-4 py-3 text-left">Request ID</th>
                  <th className="px-4 py-3 text-left">Month</th>
                  <th className="px-4 py-3 text-left">Percent</th>
                  <th className="px-4 py-3 text-left">Amount</th>
                  <th className="px-4 py-3 text-left">Status</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-emerald-50/80 bg-white">
                {filteredRows.map((row) => (
                  <tr key={row.id} className="hover:bg-emerald-50/50">
                    <td className="px-4 py-3 font-medium text-emerald-900">#{row.id}</td>
                    <td className="px-4 py-3 text-emerald-700">{row.month || "—"}</td>
                    <td className="px-4 py-3 text-emerald-700">{row.percent ?? "—"}</td>
                    <td className="px-4 py-3 text-emerald-900">{formatCurrency(row.amount)}</td>
                    <td className="px-4 py-3">
                      <StatusBadge status={row.status} />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </SectionCard>
    </div>
  );
}

function StatusBadge({ status }: { status: string }) {
  const normalized = status?.toLowerCase();
  const styles =
    normalized === "approved"
      ? "bg-emerald-50 text-emerald-700"
      : normalized === "paid"
      ? "bg-blue-50 text-blue-600"
      : "bg-amber-50 text-amber-700";

  return (
    <span className={`inline-flex items-center rounded-full px-3 py-1 text-xs font-semibold ${styles}`}>
      {status || "Pending"}
    </span>
  );
}

function formatCurrency(amount: number) {
  return new Intl.NumberFormat("en-US", { style: "currency", currency: "USD" }).format(amount || 0);
}
